.paginator button {
    height: 27px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    border: 2px solid rgb(178, 91, 182);
    background-color: rgb(254, 230, 255);
    color: #000000;
    padding: 0 5px;
    margin: 0 3px;
}

.paginator button:hover {
    padding: 2px 6px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.55);
}

.pageNumber {
    padding: 2px 6px;
    margin: 0 2px;
    border: 1px solid grey;
    border-radius: 5px;
    background-color: #fff;
}

.pageNumber.selectedPage {
    font-weight: bold;
    border: 3px solid #eda5ff;
}

.pageNumber:hover {
    padding: 3px 7px;
    border-radius: 3px;
    box-shadow: 0 0 5px #9f6fab;
}