.nav {
    grid-area: n;
    background-color: #f8d8ff;
    padding: 10px 15px;
    border-right: 3px solid white;
}

.item {
    margin-top: 5px;
}

.item a {
    color: #5000b4;
    font-weight: bolder;
    font-size: larger;
    text-decoration: none;
}

.item a:hover {
    text-shadow: 0 0 10px rgb(255, 255, 255);
    font-size: 20px;
}

.item a.active {
    color: rgba(114, 0, 255, 0.73);
}

