.usersBlock {
    background: linear-gradient(#f3eaff, #f3eaff, #f8d8ff);
}

.usersDiv {
    padding: 15px;
}

.userPhoto {
    width: 100px;
    height: 100px;
    border-radius: 10px;
    border: 3px solid #eda5ff;
}

.usersList {
}

.userBlock button {
    border-radius: 5px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.38);
    border: none;
    background-color: rgb(255, 230, 255);
    color: #000000;
    margin: 5px 0;
    padding: 3px 8px;
}

.userBlock button:hover {
    border-radius: 7px;
    box-shadow: 0 0 5px rgba(162, 0, 215, 0.55);
    background-color: #fff;
}

.userBlock {
    margin-top: 15px;
}


