.header {
    grid-area: h;
    background: linear-gradient(#9cb2fc, #f8d8ff);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    border-bottom: 3px solid white;
}

.header img {
    margin-left: 8px;
    height: 45px;
    width: 50px;
}

.name {
    font-weight: bolder;
    font-size: 22px;
    text-shadow: 0 0 10px rgba(64, 3, 77, 0.81);
}

.loginBlock {
    float: right;
    color: white;
}

.loginBlock button {
    height: 30px;
    width: 65px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.62);
    border: none;
    background-color: rgb(243, 210, 255);
    color: #000000;
}

.loginBlock button:hover {
    height: 32px;
    width: 67px;
    border-radius: 9px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.55);
}

.loginBlock a {
    color: white;
}